<!--  -->
<template>
  <div class="bargain">
    <router-link to="/" tag="p" class="bargain_top">
      <i class="el-icon-arrow-left"></i>
      <span></span>
    </router-link>
    <div style="height:0.6rem"></div>
    <div class="bargain_lists">
      <router-link
        :to="{path:'/goods',query:{id:v.id}}"
        class="bargain_list"
        v-for="(v,i) in arr"
        :key="i"
      >
        <img :src="v.pic" alt />
        <div class="bargain_right">
          <p>{{v.name}}</p>
          <p>{{v.characteristic}}</p>
          <div class="bargain_right_b">
            <div class="bargain_right_s">
              <p class="minPrice">{{v.minPrice}}.00</p>
              <p></p>
            </div>
            <div>
              <p>{{v.originalPrice}}.00</p>
              <p></p>
            </div>
            <div>
              <p>{{v.stores}}</p>
              <p></p>
            </div>
          </div>
        </div>
      </router-link>
    </div>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
import Product from "../services/views-services.js";
const _product = new Product();
export default {
  //import
  components: {},
  data() {
    //
    return {
      arr: []
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {},
  // - this
  created() {
    _product.first().then(res => {
      console.log(res.data.data);
      this.arr = res.data.data.filter(v => {
        return v.kanjia == true;
      });
      console.log(this.arr);
    });
  },
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style lang='scss' scoped>
//@import url(); css
@import url("../assets/css/bargain.css");
</style>


