<!--  -->
<template>
  <div class="zhuan">
    <router-link to="/" tag="p" class="zhuan_back">
      <i class="el-icon-arrow-left"></i>
      <span></span>
    </router-link>
    <div class="zhuan_list">
      <img :src="this.arr.pic" alt />
      <div v-html="this.arr.content"></div>
    </div>
    <Guess></Guess>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
import Product from "../services/views-services.js";
import Guess from '../components/guess'
const _product = new Product();
export default {
  //import
  components: {
    Guess,
  },
  data() {
    //
    return {
      arr: {}
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {},
  // - this
  created() {
    let { id } = this.$route.query;
    _product.jingxuanlist(id).then(res => {
      this.arr = res.data.data;
      console.log(this.arr);
    });
  },
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style lang='scss' scoped>
//@import url(); css
* {
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 0.16rem;
}
.zhuan_back {
  width: 100%;
  height: 0.5rem;
  line-height: 0.5rem;
  position: fixed;
  top: 0;
  background-color: #fff;
  i {
    margin-left: 0.2rem;
  }
  span {
    margin-left: 2.6rem;
  }
}
.zhuan_list{
    width: 95%;
    margin: 0 auto;
    img{
        width: 100%;
        height: 100%;
    }
}
</style>


