<!--  -->
<template>
  <div class>
    <p @click="back" class="purchase_back">
      <i class="el-icon-arrow-left"></i>
      <span></span>
    </p>
     <ul class="estimatelist">
        <li v-for="(v,i) in orderlist" :key="i">
            {{i}}
          <div class="estimate_img" v-for="(item,index) in orderlist[i]" :key="index" v-if="item.orderId==i">
              {{item.orderId}}
            <div class="estimate_img_top1">
              <img :src="item.pic" alt />
              {{item.id}}
              <div>
                <p>{{item.goodsName}}</p>
                <p class="property">{{item.property}}</p>
              </div>
            </div>
            <div class="estimate_estimate"></div>
          </div>
        </li>
      </ul>
    <div>
        <ul>
            <li v-for="(v,i) in this.$store.state.orderDetail" :key="i">
                <p>{{v.statusStr}}</p>
            </li>
        </ul>
    </div>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
import axios from 'axios'
export default {
  //import
  components: {},
  data() {
    //
    return {
        orderlist:[],
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    back() {
      this.$router.go(-1);
    }
  },
  // - this
  created() {
       let { id } = this.$route.query;
    this.id = id;
    let a = JSON.parse(window.localStorage.getItem("tokens"));
    let b = a.token;
    axios
      .post(`https://api.it120.cc/small4/order/list?token=${b}`)
      .then(res => {
        //   console.log(res.data.data.orderList)
        this.orderlist =res.data.data.goodsMap
        console.log(this.orderlist)
        for(let i in this.orderlist){
            this.orderlist[i].map(v=>{
                console.log(v.id)
            })
        }
      });
    console.log(this.$store.state.orderDetail)
  },
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style lang='scss' scoped>
//@import url(); css
* {
  padding: 0;
  margin: 0;
  font-size: 0.16rem;
  list-style: none;
}
.purchase_back {
  width: 100%;
  height: 0.8rem;
  line-height: 0.8rem;
  position: fixed;
  top: 0;
  background-color: #fff;
  i {
    margin-left: 0.2rem;
    font-size: 0.3rem;
  }
  span {
    margin-left: 2.4rem;
  }
}
</style>


