<!--  -->
<template>
  <div class="goods">
    <div class="goodss">
      <!-- <router-link to="/" tag="p"> -->
      <p class="goods_back" @click="back">
        <i class="el-icon-arrow-left"></i>
      </p>
      <!-- </router-link> -->
      <van-swipe :autoplay="3000" indicator-color="white" class="block">
        <van-swipe-item v-for="(v,i) in goodsimg" :key="i">
          <img :src="v.pic" alt />
        </van-swipe-item>
      </van-swipe>
      <div v-show="!showadd" class="detal_infos">
        <div class="detal_infos_top">
          <img :src="this.goodsinfo.pic" alt />
          <div>
            <p class="goodsinfo_name" v-html="this.goodsinfo.name"></p>
            <p ref="changeprice" class="goodsinfo_price">{{this.goodsinfo.minPrice}}</p>
          </div>
          <span @click="hideadd">X</span>
        </div>
        <div class="goods_type" v-for="(v,i) in type" :key="i">
          <p>{{v.name}}</p>
          <div v-show="v.childsCurGoods.length>1">
            <span
              :class="indexs==item.id+','+item.name?'typesizes':'typesize'"
              @click="sizes(item,index)"
              v-for="(item,index) in v.childsCurGoods"
              :key="index"
            >{{item.name}}</span>
          </div>
          <div v-show="v.childsCurGoods.length<=1">
            <span
              :class="indexs1==item.id+','+item.name?'typesizes':'typesize'"
              @click="sizee(item,index)"
              v-for="(item,index) in v.childsCurGoods"
              :key="index"
            >{{item.name}}</span>
          </div>
        </div>
        <div class="detal_infos_bottom">
          <span></span>
          <p>
            <button @click="jiannum">-</button>
            <span>{{this.num}}</span>
            <button @click="addnum">+</button>
          </p>
        </div>
        <p class="detal_addcar" @click="add"></p>
      </div>
      <div class="goods_infos" v-show="showadd">
        <p class="goods_infos_p1">{{this.goodsinfo.name}}</p>
        <p class="goods_infos_p2">{{this.goodsinfo.characteristic}}</p>
        <p class="goods_infos_p3">
          <span>
            
            <span>{{this.goodsinfo.minPrice}}</span>
          </span>
          <span>{{this.goodsinfo.originalPrice}}.00</span>
          <span>{{this.goodsinfo.minScore}}</span>
        </p>
      </div>
      <div class="nothing"></div>
      <div class="toggole">
        <p :class="this.cshow?'redd':'toggole_p'" @click="change"></p>
        <p :class="!this.cshow?'redd':'toggole_p'" @click="change"></p>
      </div>
      <div v-show="cshow" class="contentlist" v-html="this.content.content"></div>
      <div v-show="!cshow" class="pingjia">
        <ul>
          <li v-for="(v,i) in judgment" :key="i">
            <img src="../assets/287977.jpg" alt />
            <div>
              <p>
                <span>{{v.goods.goodReputationRemark}}</span>
                <span>{{v.goods.goodReputationStr}}</span>
              </p>
              <p>{{v.goods.dateReputation}}</p>
              <p style="color:gray,fontSize:0.14rem">:{{v.goods.property}}</p>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <div class="detal_footer" v-show="showadd">
      <span>
        <i class="el-icon-headset"></i>
      </span>
      <router-link to="/cart" tag="span">
        <i class="el-icon-shopping-cart-2"></i>
        <mark v-show="this.$store.state.addcar.length">{{this.$store.state.addcar.length}}</mark>
      </router-link>
      <span>
        <i class="el-icon-star-off"></i>
      </span>
      <span></span>
      <span class="add" @click="showadds"></span>
    </div>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
import { Dialog } from "vant";
import Product from "../services/views-services.js";
const _product = new Product();
import toLocal from "../utils/tolocal";
import axios from "axios";
function sp(v) {
  let str = v;
  let arr = str.split(",");
  let id = arr[0];
  let type = arr[1];
  return {
    id,
    type
  };
}

export default {
  //import
  components: {},
  data() {
    //
    return {
      goodsinfo: {},
      goodsimg: [],
      content: [],
      cshow: true,
      showadd: true,
      type: [],
      num: 1,
      indexs: "",
      indexs1: "",
      biggoodsid: "",
      datas: {},
      // shoplist:[],
      judgment: []
    };
  },
  // data
  computed: {},
  //data
  watch: {
    "$store.state.addcar": {
      handler: function() {
        toLocal.save("newaddcar", this.$store.state.addcar);
      },
      deep: true
    }
  },
  //
  methods: {
    sizes(item, index) {
      this.indexs = item.id + "," + item.name;
      console.log(item.id, item.propertyId);
      console.log(this.goodsinfo.id);
      let arr = {
        goodid: this.goodsinfo.id,
        proprty: item.id,
        childs: item.propertyId
      };
      _product.detailprice(arr).then(res => {
        this.datas = res.data.data;
        window.localStorage.setItem("goodsinfo", JSON.stringify(this.datas));
        console.log(this.datas);
        this.$refs.changeprice.innerHTML = "" + res.data.data.price;
      });
    },
    sizee(item, index) {
      this.indexs1 = item.id + "," + item.name;
      // if(this.type.length>1){
      //     let arr = {
      //     goodid:this.goodsinfo.id,
      //     proprty:item.id,
      //     childs:item.propertyId
      //   }
      //   _product.detailprice(arr).then(res=>{
      //     this.datas = res.data.data
      //     console.log(res.data.data)
      //     this.$refs.changeprice.innerHTML="" + res.data.data.price
      //   })
      // }
    },
    jiannum() {
      this.num--;
      if (this.num < 1) {
        this.num = 1;
      }
    },
    change() {
      this.cshow = !this.cshow;
    },
    back() {
      this.$router.go(-1);
    },
    showadds() {
      this.showadd = false;
    },
    hideadd() {
      this.showadd = true;
    },
    add() {
      if (this.type.length > 1) {
        if (!this.indexs) {
          // alert("");
          Dialog.alert({
            // title: "",
            message: ""
          }).then(() => {
            // on close
          });
          return;
        }
        if (!this.indexs1) {
          // alert("");
          Dialog.alert({
            // title: "",
            message: ""
          }).then(() => {
            // on close
          });
          return;
        }
      } else if ((this.type.length = 1)) {
        if (!this.indexs && !this.indexs1) {
          // alert("");
          Dialog.alert({
            // title: "",
            message: ""
          }).then(() => {
            // on close
          });
          return;
        }
      }
      let huang = sp(this.indexs);
      let chuan = sp(this.indexs1);
      let obj = {
        pic: this.goodsinfo.pic,
        name: this.goodsinfo.name,
        price: this.goodsinfo.minPrice,
        num: this.num,
        check: true,
        sizeid: huang.id,
        sizestr: huang.type,
        colorid: chuan.id,
        colorstr: chuan.type,
        id: this.goodsinfo.id,
        time: new Date().getTime(),
        bigid: this.biggoodsid
      };
      this.indexs = "";
      this.indexs1 = "";
      Dialog.alert({
        // title: "",
        message: ""
      }).then(() => {
        // on close
      });
      // alert("");
      this.showadd = true;
      this.$store.commit("carlist", obj);
    },
    addnum() {
      this.num++;
    }
  },
  // - this
  created() {
    let { id } = this.$route.query;
    this.biggoodsid = id;
    _product.detail(id).then(res => {
      this.goodsinfo = res.data.data.basicInfo;
      this.goodsimg = res.data.data.pics;
      this.content = res.data.data;
      this.type = res.data.data.properties;
    });

    _product.goodsjudgment(id).then(res => {
      this.judgment = res.data.data;
      this.judgment.forEach(v => {
        console.log(v);
      });
    });
  },
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style lang='scss' scoped>
//@import url(); css
@import url("../assets/css/goods.css");
.detal_footer {
  width: 100%;
  height: 0.6rem;
  position: fixed;
  bottom: 0rem;
  z-index: 25;
  text-align: center;
  display: flex;
  justify-content: space-around;
  line-height: 0.6rem;
  color: black;
  background-color: #fff;
  font-size: 0.25rem;
  span {
    border-right: 1px solid #eee;
    width: 20%;
    i {
      font-size: 0.3rem;
      color: gray;
    }
  }
  .add {
    background-color: darkred;
    color: #fff;
  }
}
.detal_infos {
  position: fixed;
  background-color: white;
  width: 100%;
  bottom: 0;
  margin: 0 auto;
  .detal_infos_top {
    width: 95%;
    margin: 0 auto;
    height: 1.8rem;
    border-bottom: 1px solid #ddd;
    display: flex;
    img {
      width: 1.5rem;
      height: 1.5rem;
      margin-top: 0.15rem;
    }
    div {
      margin-left: 0.5rem;
      height: 1.8rem;
      line-height: 0.5rem;
      .goodsinfo_name {
        margin-top: 0.5rem;
      }
      .goodsinfo_price {
        color: red;
        font-weight: bolder;
      }
    }
    span {
      margin-left: 1.2rem;
      margin-top: 0.2rem;
      width: 0.3rem;
      height: 0.3rem;
      border: 1px solid #eee;
      text-align: center;
      border-radius: 50%;
      color: gray;
    }
  }
  .goods_type {
    p {
      margin: 0.2rem;
    }
    div {
      display: flex;
      flex-wrap: wrap;
      border-bottom: 1px solid #ddd;
      padding-bottom: 0.1rem;
      .typesize {
        width: 35%;
        border: 1px solid #ddd;
        text-align: center;
        margin: 0.2rem;
        padding: 0.2rem;
        border-radius: 0.1rem;
      }
      .typesizes {
        width: 35%;
        border: 1px solid red;
        text-align: center;
        margin: 0.2rem;
        padding: 0.2rem;
        border-radius: 0.1rem;
      }
    }
  }
}
.detal_infos_bottom {
  width: 100%;
  height: 1rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
  p {
    button {
      width: 0.5rem;
      height: 0.5rem;
      background-color: #eee;
      border: none;
      border-radius: 0.05rem;
    }
    span {
      width: 0.5rem;
      height: 0.5rem;
    }
  }
}
.detal_addcar {
  width: 100%;
  height: 0.6rem;
  text-align: center;
  line-height: 0.6rem;
  color: white;
  background-color: red;
}
mark {
  background-color: red;
  border-radius: 50%;
  color: white;
}
.pingjia {
  width: 100%;
  ul {
    li {
      width: 100%;
      height: 1.6rem;
      border-bottom: 1px solid #ddd;
      display: flex;
      align-items: center;
      justify-content: center;
      img {
        width: 0.6rem;
        height: 0.6rem;
      }
      div {
        width: 60%;
        margin-left: 0.6rem;
        line-height: 0.4rem;
      }
    }
  }
}
</style>


