<!--  -->
<template>
  <div class="login">
    <p class="login_zhanghao"></p>
    <p class="login_name">krean - welcome</p>
    <input class="login_input" type="text" v-model="loginphone" placeholder="" name id />
    <br />
    <input class="login_input" type="password" v-model="loginpass" placeholder="" name id />
    <p class="login_button" @click="login"></p>
    <router-link to="/" tag="p"></router-link>
    <router-link to="register" tag="p" style="color:blue">?</router-link>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
import axios from "axios";
export default {
  //import
  components: {},
  data() {
    //
    return {
      loginphone: "",
      loginpass: ""
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    login() {
      axios
        .post(
          `https://api.it120.cc/small4/user/m/login?deviceId=007&deviceName=monkey&mobile=${this.loginphone}&pwd=${this.loginpass}`
        )
        .then(res => {
          console.log(res.data);
          if (res.data.code == 0) {
            localStorage.setItem("tokens", JSON.stringify(res.data.data));
            this.$store.state.tokens = res.data.data.token;
            this.$router.push({
              path: "/user"
            });
          } else {
            alert("");
          }
        });
    }
  },
  // - this
  created() {},
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style lang='scss' scoped>
//@import url(); css
* {
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 0.16rem;
}
.login {
  width: 100%;
  text-align: center;
  line-height: 1rem;
  .login_zhanghao {
    font-size: 0.3rem;
    font-weight: bold;
    text-align: start;
    margin-left: 1rem;
  }
  .login_name {
    text-align: start;
    margin-left: 1rem;
  }
  .login_input {
    width: 60%;
    height: 0.5rem;
    text-indent: 0.2rem;
    border-radius: 0.1rem;
    border: none;
    border: 1px solid #ddd;
  }
  .login_button {
    width: 20%;
    height: 0.6rem;
    line-height: 0.6rem;
    border-radius: 0.1rem;
    margin-left: 2.6rem;
    text-align: center;
    color: white;
    background: linear-gradient(to right, #fff 0%, #000 100%);
  }
}
</style>


