<!--  -->
<template>
  <div class="footer">
    <ul>
      <router-link to="/" tag="li" active-class="active" exact>
        <i class="el-icon-house"></i>
        <p></p>
      </router-link>
      <router-link to="/classify" tag="li" active-class="active">
        <i class="el-icon-box"></i>
        <p></p>
      </router-link>
      <router-link to="/cart" tag="li" active-class="active">
        <i class="el-icon-shopping-cart-2"></i>
        <mark v-show="this.$store.state.addcar.length">{{this.$store.state.addcar.length}}</mark>
        <p></p>
      </router-link>
      <router-link to="/user" tag="li" active-class="active">
        <i class="el-icon-coordinate"></i>
        <p></p>
      </router-link>
    </ul>
    <transition class="slide-fade">
        <router-view></router-view>
    </transition>
  </div>
</template>

<script>
//jsjsjson
//import  from '';

export default {
  //import
  components: {},
  data() {
    //
    return {};
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {},
  // - this
  created() {},
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style lang='scss' scoped>
//@import url(); css
@import url("../assets/css/footer.css");
.slide-fade {
  position: absolute;
  left: 0;
  right: 0;
}
.slide-fade-enter-active {
  transition: all1.2sease;
}
.slide-fade-leave-active {
  transition: all.1scubic-bezier(2, 0.5, 0.8, 1.0);
}
.slide-fade-enter,
.slide-fade-leave-to {
  left: 0;
  right: 0;
  transform: translateX(50px);
  opacity: 0;
}
mark {
  background-color: red;
  border-radius: 50%;
  color: white;
}
</style>


