<!--  -->
<template>
  <div class="classifydetail">
    <p class="classifydetail_top" @click="back">
      <i class="el-icon-arrow-left"></i>
      <span></span>
    </p>
    <div class="none_classify" v-show="!classifydetail.length">
        
    </div>
    <div class="cars_lists" v-show="classifydetail.length">
      <ul>
        <router-link
          :to="{path:'/detal',query:{id:v.id}}"
          v-for="(v,i) in classifydetail"
          :key="i"
          tag="li"
        >
          <img :src="v.pic" alt />
          <div>
            <p class="name" v-html="v.name"></p>
            <p class="miaoshu">{{v.characteristic}}</p>
            <p class="num">{{v.minPrice}}0</p>
          </div>
        </router-link>
      </ul>
    </div>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
import Product from "../services/views-services";
const _product = new Product();
export default {
  //import
  components: {},
  data() {
    //
    return {
      classifydetail: []
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
      back(){
          this.$router.go(-1)
      }
  },
  // - this
  created() {
    let id = this.$route.query.id;
    console.log(id);
    _product.first().then(res => {
      this.classifydetail = res.data.data.filter(v => {
        return v.categoryId == id;
      });
      console.log(this.classifydetail);
    });
  },
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style lang='scss' scoped>
//@import url(); css
* {
  padding: 0;
  margin: 0;
  list-style: none;
  font-size: 0.16rem;
}
.none_classify{
    width: 100%;
    height: 5rem;
    text-align: center;
    line-height: 5rem;
}
.classifydetail_top {
  width: 100%;
  height: 0.5rem;
  line-height: 0.5rem;
  top: 0;
  background-color: #fff;
  i {
    margin-left: 0.2rem;
  }
  span {
    margin-left: 2.6rem;
  }
}
.cars_lists {
  width: 98%;
  margin: 0 auto;
  ul {
    display: flex;
    flex-wrap: wrap;
    li {
      width: 48%;
      height: 3.5rem;
      position: relative;
      margin-right: 0.12rem;
      img {
        width: 3rem;
        height: 3rem;
      }
      div {
        position: absolute;
        top: 2.1rem;
        p {
          margin-top: 0.1rem;
          width: 3rem;
          overflow: hidden;
        }
        .name {
          height: 0.4rem;
          line-height: 0.4rem;
          background-color: goldenrod;
        }
        .miaoshu {
          width: 3rem;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        .num {
          color: red;
          font-weight: bolder;
        }
      }
    }
  }
}
</style>


