<!--  -->
<template>
  <div class="addaddress">
    <p @click="back" class="purchase_back">
      <i class="el-icon-arrow-left"></i>
      <span></span>
    </p>
    <div class="addaddress_bottom">
      <p>
        <span></span>
        <input type="text" v-model="user" placeholder="" name id />
      </p>
      <p>
        <span></span>
        <input type="text" v-model="phone" placeholder="11" maxlength="11" name id />
      </p>
      <div @click="show=true" class="chose_address">
        <p></p>
        <div class="cityinfo">
          <p v-for="(v,i) in address" :key="i">{{v.name}}</p>
        </div>
      </div>
      <div class="address">
        <van-popup v-model="show" position="bottom" :style="{height:'40%',width:'100%'}">
          <van-area @cancel="quxiao" @confirm="getaddress" :area-list="san" />
        </van-popup>
      </div>
      <p>
        <span></span>
        <input type="text" v-model="door" placeholder="" name id />
      </p>
      <p>
        <span></span>
        <input type="text" v-model="express" placeholder="" name id />
      </p>
      <button style="background:darkgreen" @click="keeps"></button>
      <button style="background:darkred"></button>
      <button style="background:gray" @click="cancle"></button>
    </div>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
import Product from "../services/views-services";
import sanji from "../utils/sanji";
const _product = new Product();
export default {
  //import
  components: {},
  data() {
    //
    return {
      user: "",
      phone: "",
      door: "",
      express: "",
      show: false,
      san: sanji,
      address: [],
      token: "",
      myaddresses: [],
      defaults:true
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    back() {
      this.$router.go(-1);
    },
    quxiao() {
      this.show = false;
    },
    getaddress(val) {
      this.address = val;
      console.log(this.address);
      console.log(this.address[0].name);
      console.log(this.address[1].name);
      // console.log(this.address)
    },
    keeps() {
      let obj = {
        user: this.user,
        phone: this.phone,
        cityIds: this.address[1].code,
        door: this.door,
        express: this.express,
        provinceIds: this.address[0].code,
        tokens: this.token,
        default: this.defaults
      };
      console.log(obj);
      _product.addaddress(obj).then(res => {
        let a = res.data.data;
        this.myaddresses.push(a);
        this.$store.state.address = this.myaddresses;
        window.localStorage.setItem(
          "myaddress",
          JSON.stringify(this.$store.state.address)
        );
        console.log(this.$store.state.address);
        if (res.data.code == 0) {
          this.$router.push({
            path: "/user_address"
          });
        }
      });
    },
    cancle() {
      (this.user = ""),
        (this.phone = ""),
        (this.door = ""),
        (this.express = "");
    }
  },
  // - this
  created() {
    let a = JSON.parse(localStorage.getItem("tokens"));
    this.token = a.token;
    console.log(this.token);
  },
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style lang='scss' scoped>
//@import url(); css
* {
  padding: 0;
  margin: 0;
  font-size: 0.16rem;
  list-style: none;
}
.purchase_back {
  width: 100%;
  height: 0.8rem;
  line-height: 0.8rem;
  position: fixed;
  top: 0;
  background-color: #fff;
  i {
    margin-left: 0.2rem;
    font-size: 0.3rem;
  }
  span {
    margin-left: 2.4rem;
  }
}
.addaddress_bottom {
  width: 95%;
  margin: 0 auto;
  margin-top: 0.9rem;
  line-height: 1rem;
  span {
    margin-left: 0.2rem;
  }
  input {
    border: none;
    outline: nonoe;
  }
  button {
    width: 100%;
    height: 0.8rem;
    line-height: 0.8rem;
    color: white;
    border: none;
    border-radius: 0.1rem;
    margin-bottom: 0.3rem;
  }
}
.chose_address {
  display: flex;
  margin-left: 0.2rem;
}
.cityinfo {
  display: flex;
  margin-left: 0.2rem;
}
</style>


