<!--  -->
<template>
  <div class>
    <p @click="back" class="purchase_back">
      <i class="el-icon-arrow-left"></i>
      <span></span>
    </p>
    <div class="orederlist">
      <ul>
        <li v-for="(v,i) in shuxing" :key="i">
          <div class>
            <div v-for="(item,index) in shuxing[i]" :key="index" class="list_img">
              <img :src="item.pic" alt />
              <div>
                <p class="property">{{item.goodsName}}</p>
                <p class="property">{{item.property}}</p>
              </div>
            </div>
          </div>
          <div
            class="list_status"
            v-if="item2.id==i"
            v-for="(item2,index2)  in orderlist"
            :key="index2"
          >
            <!-- <router-link to="/estimate"></router-link> -->
            <p>{{item2.statusStr}}</p>
            <mark v-text="item2.statusStr==''?'':''" @click="gotoestimate(item2.id)"></mark>
            <p>{{item2.orderNumber}}</p>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
import Product from "../services/views-services";
const _product = new Product();
import axios from "axios";
export default {
  //import
  components: {},
  data() {
    //
    return {
      shuxing: "",
      orderlist: []
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    gotoestimate(ids) {
      this.$router.push({
        path:"/estimate",
        query:{
          id:ids
        }
      })
      console.log(ids);
    },
    back() {
      this.$router.go(-1);
    }
  },
  // - this
  created() {
    let a = JSON.parse(window.localStorage.getItem("tokens"));
    let b = a.token;
    axios
      .post(`https://api.it120.cc/small4/order/list?token=${b}`)
      .then(res => {
        console.log(res.data.data);
        console.log(res.data.data.orderList);
        this.shuxing = res.data.data.goodsMap;
        let order = Object.keys(this.shuxing);
        console.log(order);
        this.orderlist = res.data.data.orderList.filter(v => {
          return order.map(j => {
            return v.orderId == j;
          });
        });
        // this.$store.state.orderDetail=this.orderlist.filter(v=>{
        //   return v.statusStr == ""
        // })
        // console.log(this.$store.state.orderDetail)
      });
    // console.log(this.$store.state.orderNumber.orderNumber)
  },
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style lang='scss' scoped>
//@import url(); css
* {
  padding: 0;
  margin: 0;
  font-size: 0.16rem;
  list-style: none;
}
.purchase_back {
  width: 100%;
  height: 0.8rem;
  line-height: 0.8rem;
  position: fixed;
  top: 0;
  background-color: #fff;
  i {
    margin-left: 0.2rem;
    font-size: 0.3rem;
  }
  span {
    margin-left: 2.4rem;
  }
}
.orederlist {
  margin-top: 0.8rem;
  ul {
    width: 100%;
    li {
      width: 95%;
      margin: 0 auto;
      display: flex;
      margin-top: 0.1rem;
      border-bottom: 1px solid #ddd;
      padding-bottom: 0.1rem;
      align-items: center;
      .list_img {
        width: 100%;
        display: flex;
        justify-content: space-around;
        align-items: center;
        margin-block-start: 0.1rem;
        img {
          width: 1rem;
          height: 1rem;
        }
        div {
          line-height: 0.4rem;
          margin-left: 0.2rem;
          .property {
            width: 2.5rem;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
          }
        }
      }
      .list_status {
        width: 20%;
        margin-left: 0.2rem;
      }
    }
  }
}
</style>


