<!--  -->
<template>
  <div class>
    <First></First>
    <Footer></Footer>
    <transition >
      <router-view></router-view>
    </transition>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
import First from "../components/first";
import Footer from "../components/footer";
export default {
  //import
  components: {
    First,
    Footer
  },
  data() {
    //
    return {};
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {},
  // - this
  created() {},
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style lang='scss' scoped>
//@import url(); css
// .slide-fade {
//   position: absolute;
//   left: 0;
//   right: 0;
// }
.v-enter-active {
  transition: all1.2sease;
}
.v-leave-active {
  transition: all1scubic-bezier(2, 0.5, 0.8, 1.0);
}
.v-enter{
  // left: 0;
  // right: 0;
  transform: translateX(100%);
  opacity: 0;
}
.v-leave-to {
  // left: 0;
  // right: 0;
  transform: translateX(50px);
  opacity: 0;
}
</style>


