import HTTP from '../utils/http'
const _http = new HTTP()
class Product {
  // list
  list () {
    return _http.request({
      url: 'banner/list'
    })
  }
  //first_list
  first(){
    return _http.request({
      url:"shop/goods/list",
    })
  }
  // detail
  detail (productId) {
    return _http.request({
      type: 'post',
      url: 'shop/goods/detail',
      data: {
        id: productId
      }
    })
  }
  //
  jingxuan(){
    return _http.request({
      url:"cms/news/list"
    })
  }
  //
  jingxuanlist(productId){
    return _http.request({
      type: 'post',
      url: 'cms/news/detail',
      data: {
        id: productId
      }
    })
  }
  //
  fenlei(){
    return _http.request({
      url:"shop/goods/category/all"
    })
  }
  //
  appraise(goodsId){
    return _http.request({
      type:"post",
      url:'shop/goods/reputation',
      data:{
        goodsId,
      }
    })
  }
  //
  detailprice(goodsIds){
    return _http.request({
      type:"post",
      url:"shop/goods/price",
      data:{
        goodsId:goodsIds.goodid,
        propertyChildIds:goodsIds.proprty+","+goodsIds.childs
      }
    })
  }
  //
  appra(goodsid){
    return _http.request({
      type:"post",
      url:"shop/goods/reputation",
      data:{
        goodsid
      }
    })
  }
  //
  order(obj){
    return _http.request({
      type:'post',
      url:"order/reputation",
      data:{
        token:obj.token,
        postJsonString:obj.postJsonString
      }
    })
  }
  //
  getorder(oinformation){
    return _http.request({
      type:"post",
      url:"order/create",
      data:{
        token:oinformation.token,
        goodsJsonStr:oinformation.a
      }
    })
  }
  //
  orderlist(tokensid){
    return _http.request({
      type:"post",
      url:"order/list",
      data:{
        tokensid
      }
    })
  }
  //
  addaddress(add){
    return _http.request({
      type:"post",
      url:"user/shipping-address/add",
      data:{
        address:add.door,
        cityId:add.cityIds,
        code:add.express,
        linkMan:add.user,
        mobile:add.phone,
        provinceId:add.provinceIds,
        token:add.tokens,
        isDefault:add.default
      }
    })
  }
  //
  addresslist(tokens){
    return _http.request({
      type:'post',
      url:"user/shipping-address/list",
      data:{
        token:tokens
      }
    })
  }
  //
  deladdress(ids){
    return _http.request({
      type:'post',
      url:'user/shipping-address/delete',
      data:{
        token:ids.tokens,
        id:ids.ids
      }
    })
  }
  //
  defaultadd(tokens){
    return _http.request({
      type:'post',
      url:"user/shipping-address/default",
      data:{
        token:tokens
      }
    })
  }
  //
  goodsjudgment(id){
    return _http.request({
      type:"post",
      url:"shop/goods/reputation",
      data:{
        goodsId:id
      }
    })
  }
  //
  kanjialist(){
    return _http.request({
      url:"shop/goods/kanjia/list"
    })
  }
  //
  initiate(kanjia){
    return _http.request({
      type:"post",
      url:"shop/goods/kanjia/join",
      data:{
        kjid:kanjia.kjid,
        token:kanjia.token
      }
    })
  }
  //
  kanjiainfo(details){
    return _http.request({
      type:'post',
      url:"shop/goods/kanjia/info",
      data:{
        kjid:details.kjid,
        joiner:details.tokenid
      }
    })
  }
  //
  cutone(cutoneid){
    return _http.request({
      type:"post",
      url:"shop/goods/kanjia/help",
      data:{
        token:cutoneid.token,
        kjid:cutoneid.kjid,
        joinerUser:cutoneid.join,
      }
    })
  }
}

export default Product



