<!--  -->
<template>
  <div class="car">
    <div class="car_bottom">
      <p></p>
      <div class="cars_lists">
        <ul>
          <router-link
            :to="{path:'/detal',query:{id:v.id}}"
            v-for="(v,i) in list"
            :key="i"
            tag="li"
          >
            <img :src="v.pic" alt />
            <div>
              <p class="name" v-html="v.name"></p>
              <p class="miaoshu">{{v.characteristic}}</p>
              <p class="num">{{v.minPrice}}0</p>
            </div>
          </router-link>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
import Product from "../services/views-services";
const _product = new Product();
export default {
  //import
  components: {},
  data() {
    //
    return {
      list: [],
      suiji: parseInt(Math.random() * 27)
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {},
  // - this
  created() {
    _product.first().then(res => {
      this.list = res.data.data.splice(this.suiji, 4);
      console.log(this.list);
    });
  },
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style lang='scss' scoped>
//@import url(); css
* {
  padding: 0;
  margin: 0;
  list-style: none;
  font-size: 0.16rem;
}
.car {
  width: 100%;
  .nonecart {
    width: 100%;
    height: 3rem;
    line-height: 3rem;
    text-align: center;
    background-color: #eee;
    color: gray;
    margin-bottom: 0.1rem;
  }
  .car_bottom {
    width: 100%;
    text-align: center;
    .cars_lists {
      width: 98%;
      margin: 0 auto;
      ul {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        li {
          width: 48%;
          height: 3.5rem;
          position: relative;
          img {
            width: 3rem;
            height: 3rem;
          }
          div {
            position: absolute;
            top: 2.1rem;
            p {
              margin-top: 0.1rem;
              width: 3rem;
              overflow: hidden;
            }
            .name {
              height: 0.4rem;
              line-height: 0.4rem;
              background-color: goldenrod;
            }
            .miaoshu {
              width: 3rem;
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
            }
            .num {
              color: red;
              font-weight: bolder;
            }
          }
        }
      }
    }
  }
}
</style>


