<!--  -->
<template>
  <div class="payment">
    <p @click="back" class="purchase_back">
      <i class="el-icon-arrow-left"></i>
      <span></span>
    </p>
    <div class="payment_money">
      <span class="el-icon-wallet"></span>
      <div>
        <p>
          <span></span>
          <span>{{this.$store.getters.checkprice}}.00</span>
        </p>
        <p>:{{this.$store.state.orderNumber.orderNumber}}</p>
      </div>
    </div>
    <div class="payment_address">
      <i class="el-icon-location-information"></i>
      <div>
        <p>{{this.$store.state.defaultress.linkMan}}{{this.$store.state.defaultress.mobile}}</p>
        <p>{{this.$store.state.defaultress.provinceStr}}{{this.$store.state.defaultress.cityStr}}{{this.$store.state.defaultress.areaStr}}</p>
      </div>
    </div>
    <!-- <router-link to="/orders" class="payment_bottom" tag=""></router-link> -->
    <div class="payment_bottom" @click="jump"> {{this.$store.getters.checkprice}}.00</div>
  </div>
</template>

<script>
//jsjsjson
//import  from '';

export default {
  //import
  components: {},
  data() {
    //
    return {};
  },
  // data
  computed: {
    tureaddress() {
      return this.$store.getters.defaultaddress;
    }
  },
  //data
  watch: {},
  //
  methods: {
    back() {
      this.$router.go(-1);
    },
    jump() {
      this.$router.push({
        path: "/orders"
      });
      this.$store.commit("checkdel");
    }
  },
  // - this
  created() {
    console.log(this.$store.state.orderNumber);
  },
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style lang='scss' scoped>
//@import url(); css
* {
  margin: 0;
  padding: 0;
  font-size: 0.16rem;
  list-style: none;
}
.purchase_back {
  width: 100%;
  height: 0.8rem;
  line-height: 0.8rem;
  position: fixed;
  top: 0;
  background-color: #fff;
  i {
    margin-left: 0.2rem;
    font-size: 0.3rem;
  }
  span {
    margin-left: 2.4rem;
  }
}
.payment_money {
  margin-top: 0.9rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  padding: 0 0.2777777778rem;
  height: 1.5972222222rem;
  border-top: 1px solid #e9e9e9;
  border-bottom: 0.1041666667rem solid #fbfbfb;
  .el-icon-wallet {
    font-size: 0.6rem;
    color: red;
  }
  div {
    margin-left: 0.4rem;
    line-height: 0.4rem;
    span:nth-child(2) {
      color: red;
    }
  }
}
.payment_address {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  padding: 0 0.2777777778rem;
  height: 1.5972222222rem;
  border-top: 1px solid #e9e9e9;
  border-bottom: 0.1041666667rem solid #fbfbfb;
  .el-icon-location-information {
    font-size: 0.6rem;
    color: blue;
  }
  div {
    margin-left: 0.4rem;
    line-height: 0.4rem;
  }
}
.payment_bottom {
  width: 100%;
  height: 0.8rem;
  background-color: darkred;
  text-align: center;
  line-height: 0.8rem;
  color: white;
  position: fixed;
  bottom: 0;
}
</style>


