<template>
<div class='gift'>
     <router-link to="/" tag="p" class="gift_p"> 
        <i class="el-icon-arrow-left"></i>
    </router-link>
    <div class="gift_top">
        <div>
            <p></p>
            <button></button>
        </div>
    </div>
    <div class="gift_quan">

    </div>
</div>
</template>

<script>
//jsjsjson
//import  from '';

export default {
//import
components: {},
data() {
//
return {

};
},
// data
computed: {},
//data
watch: {},
//
methods: {

},
// - this
created() {

},
// - DOM
mounted() {

},
beforeCreate() {}, // - 
beforeMount() {}, // - 
beforeUpdate() {}, // - 
updated() {}, // - 
beforeDestroy() {}, // - 
destroyed() {}, // - 
activated() {}, //keep-alive
}
