<style lang='scss' scoped>
//@import url(); css
*{
    padding: 0;
    margin: 0;
    list-style: none;
    font-size: 0.16rem
}
.gift{
    width: 100%;
}
.gift_p{
    width: 0.3rem;
    height: 0.3rem;
    position: fixed;
    z-index: 2;
    top: 0.1rem;
    left: 0.1rem;
    background-color: #fff;
    border-radius: 50%;
    text-align: center;
    line-height: 0.3rem;

    i {
      font-size: 0.2rem;
    }
}
.gift_top{
    width: 100%;
    height: 5rem;
    background: url("../assets/287977.jpg") no-repeat;
    background-size: 100% 100%;
    box-sizing: border-box;
    padding: 4rem 0 0 0;
    div{
        width: 95%;
        height: 1rem;
        margin: 0 auto;
        border: 0.02rem solid #F1CC93;
        box-sizing: border-box;
        padding: 0.1rem;
        display: flex;
        justify-content: space-around;
        align-items: center;
        background-color: white;
        p{
            width: 70%;
            height: 0.3rem;
            border-radius: 0.05rem;
            text-indent: 0.1rem;
            line-height: 0.3rem;
            color: #F1CC93;
            border: 1px solid rebeccapurple
        }
        button{
            width: 20%;
            height: 0.3rem;
            background-color: #EE7C7B;
            color: white;
            border: none;
            border-radius: 0.05rem
        }
    }
}
</style>


