<!--  -->
<template>
  <div class="register">
    <p class="register_new"></p>
    <p class="register_happey">()</p>
    <input
      class="login_input"
      :class="phones?'aa':'bb'"
      type="text"
      @blur="testphone"
      v-model="phone"
      placeholder=""
      maxlength="11"
    />
    <br />
    <input
      class="login_input"
      maxlength="16"
      :class="passes?'aa':'bb'"
      @blur="testpass"
      type="password"
      v-model="pass"
      placeholder="(8-16)"
    />
    <br />
    <input
      class="login_input"
      :class="checkp?'aa':'bb'"
      @blur="checkpass"
      type="password"
      v-model="sure"
      placeholder=""
    />
    <br />
    <input
      class="login_input"
      maxlength="6"
      type="text"
      :class="users?'aa':'bb'"
      @blur="useres"
      v-model="user"
      placeholder="6"
    />
    <br />
    <input :class="pictrue?'aa':'bb'" @blur="pictrues" class="login_input" maxlength="6" type="text" v-model="pic" placeholder="" name id />
    <br />
    <div @click="show=true" class="chose_address">
      <p></p>
      <div>
        <p v-for="(v,i) in address" :key="i">{{v.name}}</p>
      </div>
    </div>
    <div class="address">
      <van-popup v-model="show" position="bottom" :style="{height:'40%',width:'100%'}">
        <van-area @cancel="quxiao" @confirm="getaddress" :area-list="san" />
      </van-popup>
    </div>
    <div class="yanzheng">
      <input type="text" v-model="trueCode" maxlength="4" name id />
      <p @click="get"></p>
    </div>

    <p class="register_button" @click="siginnow"></p>
    <router-link to="/login" tag="p" style="color:blue">?</router-link>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
import axios from "axios";
import sanji from "../utils/sanji";
export default {
  //import
  components: {},
  data() {
    //
    return {
      yanzheng: "",
      phone: "",
      pass: "",
      sure: "",
      user: "",
      pic: "",
      time: "",
      trueCode: "",
      show: false,
      san: sanji,
      address: [],
      //
      phones: true,
      passes:true,
      checkp:true,
      users:true,
      pictrue:true,
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    testphone() {
      if(!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.phone))) {
        this.phones = false;
        return false
      }else{
        this.phones = true;
      }
    },
    testpass(){
       if(!(/^\d{8,16}$/.test(this.pass))) {
        this.passes = false;
        return false
      }else{
        this.passes = true;
      }
    },
    checkpass(){
      if(this.pass!=this.sure){
        this.checkp=false
        return false
      }else{
        this.checkp=true
      }
    },
    useres(){
       if(!(/^\d{6}$/.test(this.user))) {
        this.users = false;
        return false
      }else{
        this.users = true;
      }
    },
    pictrues(){
      
    },
    getaddress(val) {
      this.address = val;
      console.log(this.address[0].name);
      console.log(this.address[1].name);
      // console.log(this.address)
    },
    quxiao() {
      this.show = false;
    },
    change() {
      this.time = new Date().getTime();
      axios
        .get(
          `https://api.it120.cc/small4/verification/pic/get?key=${this.time}`,
          {
            responseType: "arraybuffer"
          }
        )
        .then(response => {
          this.yanzheng =
            "data:image/png;base64," +
            btoa(
              new Uint8Array(response.data).reduce(
                (data, byte) => data + String.fromCharCode(byte),
                ""
              )
            );
        });
    },
    get() {
      axios
        .post(
          `https://api.it120.cc/small4/verification/sms/get?mobile=${this.phone}&key=${this.time}&picCode=${this.pic}`
        )
        .then(res => {
          console.log(res);
        });
    },
    siginnow() {
      axios
        .post(
          `https://api.it120.cc/small4/verification/sms/check?mobile=${this.phone}&code=${this.trueCode}`
        )
        .then(res => {
          console.log(res.data);
          if (res.data.code == 0) {
            axios
              .post(
                `https://api.it120.cc/small4/user/m/register?mobile=${this.phone}&pwd=${this.pass}&code=${this.trueCode}&nick=${this.user}&province=${this.address[0].name}&city=${this.address[1].name}`
              )
              .then(res => {
                // if(res.data.code==0){
                // this.$router.push({
                //   path:"/login"
                // })
                console.log(res.data);
                // }
              });
          } else {
            axios
              .post(
                `https://api.it120.cc/small4/user/m/register?mobile=${this.phone}&pwd=${this.pass}&code=${this.trueCode}`
              )
              .then(res => {
                console.log(res.data.mag);
              });
          }
        });
    }
  },
  // - this
  created() {
    let a = new Date().getTime();
    this.time = a;
    axios
      .get(
        `https://api.it120.cc/small4/verification/pic/get?key=${this.time}`,
        {
          responseType: "arraybuffer"
        }
      )
      .then(response => {
        this.yanzheng =
          "data:image/png;base64," +
          btoa(
            new Uint8Array(response.data).reduce(
              (data, byte) => data + String.fromCharCode(byte),
              ""
            )
          );
      });
  },
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style lang='scss' scoped>
//@import url(); css
* {
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 0.16rem;
}
.register {
  text-align: center;
  width: 95%;
  .yanzheng {
    width: 100%;
    align-items: center;
    display: flex;
    justify-content: center;
    align-items: center;
    input {
      width: 60%;
      margin-left: 1.2rem;
      height: 0.5rem;
      margin-top: 0.1rem;
      text-indent: 0.2rem;
      border-radius: 0.1rem;
      border: none;
      margin-bottom: 0.2rem;
      border: 1px solid #ddd;
    }
    p {
      width: 49%;
      height: 0.5rem;
      line-height: 0.5rem;
      text-align: start;
      margin-left: 0.1rem;
    }
  }
}
.register_button {
  width: 20%;
  height: 0.6rem;
  line-height: 0.6rem;
  border-radius: 0.1rem;
  margin-left: 2.6rem;
  color: white;
  text-align: center;
  margin-top: 0.2rem;
  margin-bottom: 0.2rem;
  background: linear-gradient(to right, #fff 0%, #000 100%);
}
.login_input {
  width: 60%;
  height: 0.5rem;
  text-indent: 0.2rem;
  border-radius: 0.1rem;
  border: none;
  margin-bottom: 0.2rem;
  border: 1px solid #ddd;
}
.register_new {
  font-size: 0.3rem;
  font-weight: bold;
  text-align: start;
  margin-left: 1rem;
  margin-top: 0.4rem;
  margin-bottom: 0.3rem;
}
.register_happey {
  color: gray;
  margin-bottom: 0.3rem;
}
.chose_address {
  width: 80%;
  margin: 0 auto;
  height: 0.8rem;
  display: flex;
  align-items: center;
  justify-content: space-around;
  div {
    width: 40%;
    display: flex;
    text-align: center;
  }
}
.aa {
  width: 60%;
  height: 0.5rem;
  margin-top: 0.1rem;
  text-indent: 0.2rem;
  border-radius: 0.1rem;
  border: none;
  margin-bottom: 0.2rem;
  border: 1px solid #ddd;
}
.bb {
  width: 60%;
  height: 0.5rem;
  margin-top: 0.1rem;
  text-indent: 0.2rem;
  border-radius: 0.1rem;
  border: none;
  margin-bottom: 0.2rem;
  border: 1px solid red;
}
</style>


