<!--  -->
<template>
  <div class>
    <router-link to="/" tag="p" class="special_top">
      <i class="el-icon-arrow-left"></i>
      <span></span>
    </router-link>
    <div style="height:0.6rem"></div>
    <div class="special_lists">
      <div class="special_list" v-for="(v,i) in jingxuan" :key="i">
        <img :src="v.pic" alt />
        <div>
          <p v-html="v.title"></p>
          <p class="special_descript" v-html="v.descript"></p>
          <router-link
            :to="{path:'/zhuan',query:{id:v.id}}"
            :key="i"
            tag="p"
            class="check"
          ></router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
import Product from "../services/views-services.js";
const _product = new Product();
export default {
  //import
  components: {},
  data() {
    //
    return {
      jingxuan: []
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {},
  // - this
  created() {
    _product.jingxuan().then(res => {
      this.jingxuan = res.data.data;
    });
  },
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style lang='scss' scoped>
//@import url(); css
* {
  margin: 0;
  padding: 0;
  font-size: 0.16rem;
  list-style: none;
}
.special_top {
  width: 100%;
  height: 0.5rem;
  line-height: 0.5rem;
  position: fixed;
  top: 0;
  background-color: #fff;
  i {
    margin-left: 0.2rem;
  }
  span {
    margin-left: 2.6rem;
  }
}
.special_list {
  width: 95%;
  margin: 0 auto;
  position: relative;
  img {
    background: rgba($color: #000000, $alpha: 0.8);
    width: 100%;
    height: 4.5rem;
  }
  div {
    position: absolute;
    color: white;
    top: 1.5rem;
    text-align: center;
    p {
      font-size: 0.3rem;
    }
    .special_descript {
      width: 6rem;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      margin-top: 0.2rem;
    }
    .check {
      width: 1.6rem;
      margin-top: 0.2rem;
      height: 0.5rem;
      margin-left: 2.2rem;
      border-radius: 0.1rem;
      line-height: 0.5rem;
      border: 1px solid #fff;
      color: #fff;
    }
  }
}
</style>


