<!--  -->
<template>
  <div class="user_address">
    <p @click="back" class="purchase_back">
      <i class="el-icon-arrow-left"></i>
      <span></span>
    </p>
    <div class="addresslist">
      <ul>
        <li v-for="(v,i) in this.$store.state.addresslist" :key="i">
          <el-checkbox v-model="v.isDefault" @change="changetrue(i)"></el-checkbox>
          <!-- <input type="checkbox" name id /> -->
          <div>
            <p>{{v.linkMan}}{{v.mobile}}</p>
            <p>{{v.provinceStr}}{{v.cityStr}}{{v.address}}</p>
          </div>
          <div>
            <i class="el-icon-edit-outline"></i>
            <i class="el-icon-delete" @click="deladdress(i)"></i>
          </div>
        </li>
      </ul>
    </div>
    <router-link to="/addaddress" tag="div" class="user_address_bottom"></router-link>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
import Product from "../services/views-services";
const _product = new Product();
export default {
  //import
  components: {},
  data() {
    //
    return {
      getaddress: []
    };
  },
  // data
  computed: {
    tureaddress() {
      return this.$store.getters.defaultaddress;
    }
  },
  //data
  watch: {},
  //
  methods: {
    back() {
      this.$router.go(-1);
    },
    changetrue(i) {
      this.$store.commit("changetrue", i);
      this.$store.state.defaultress = this.tureaddress;
      window.localStorage.setItem(
        "tureaddresslist",
        JSON.stringify(this.$store.state.defaultress)
      );
    },
    deladdress(i){
      let a = this.$store.state.addresslist[i].id
       let token = JSON.parse(localStorage.getItem("tokens"));
      console.log(token)
      let tokenes = token.token
      let b = {
        tokens:tokenes,
        ids:a
      }
      _product.deladdress(b).then(res=>{
        if(res.data.code==0){
          this.$store.state.addresslist=res.data.data
        }
        // console.log(res.data)
      })
      // console.log(this.$store.state.addresslist[i].id)
    }
  },
  // - this
  created() {
    // let a = JSON.parse(window.localStorage.getItem("myaddress"));
    // this.getaddress = a;
    let a = JSON.parse(window.localStorage.getItem("tokens"));
    _product.addresslist(a.token).then(res => {
      if (res.data.code == 0) {
        this.$store.state.addresslist = res.data.data;
        console.log(this.$store.state.addresslist)
      } else {
        console.log(res.data.msg);
      }
    });

    // console.log(this.$store.state.defaultress)
  },
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style lang='scss' scoped>
//@import url(); css
* {
  margin: 0;
  padding: 0;
  font-size: 0.16rem;
  list-style: none;
}
.purchase_back {
  width: 100%;
  height: 0.8rem;
  line-height: 0.8rem;
  position: fixed;
  top: 0;
  background-color: #fff;
  i {
    margin-left: 0.2rem;
    font-size: 0.3rem;
  }
  span {
    margin-left: 2.4rem;
  }
}
.addresslist {
  width: 100%;
  margin-top: 0.8rem;
  ul {
    width: 100%;
    li {
      width: 90%;
      margin: 0 auto;
      height: 1rem;
      border-bottom: 1px solid #ddd;
      display: flex;
      justify-content: space-between;
      align-items: center;
      div {
        line-height: 0.4rem;
      }
      i {
        font-size: 0.3rem;
        color: gray;
      }
    }
  }
}
.user_address_bottom {
  width: 100%;
  height: 0.8rem;
  background-color: rebeccapurple;
  position: fixed;
  bottom: 0;
  text-align: center;
  line-height: 0.8rem;
  color: white;
}
</style>


