<!--  -->
<template>
  <div class="mine">
    <div class="mine_top">
      <p>
        <i class="el-icon-user"></i>
      </p>
      <div>
        <router-link v-show="!chack" to="/login" tag="p"></router-link>
        <p v-show="chack">{{this.user.mobile}}</p>
        <p>0</p>
      </div>
    </div>
    <div class="mine_banner">
      <i class="el-icon-orange"></i>
      <span></span>
    </div>
    <div class="nothing"></div>
    <div class="mine_order">
      <router-link to="/orders" tag="div">
        <i class="el-icon-document"></i>
        <span></span>
      </router-link>
      <i class="el-icon-arrow-right"></i>
    </div>
    <div class="mine_nav">
      <ul>
        <li>
          <i class="el-icon-bank-card"></i>
          <p></p>
        </li>
        <li>
          <i class="el-icon-collection"></i>
          <p></p>
        </li>
        <li>
          <i class="el-icon-truck"></i>
          <p></p>
        </li>
        <li @click="waitestimate">
          <i class="el-icon-chat-line-square"></i>
          <p></p>
        </li>
      </ul>
    </div>
    <div class="nothing"></div>
    <div class="mine_banner1">
      <ul>
        <li>
          <i style="color:#F86636" class="el-icon-food"></i>
          <p></p>
          <p style="color:gray">0</p>
        </li>
        <li>
          <i style="color:#F86636" class="el-icon-chicken"></i>
          <p></p>
        </li>
        <li>
          <i style="color:#EDB449" class="el-icon-sugar"></i>
          <p></p>
        </li>
        <li>
          <i style="color:#EDB449" class="el-icon-ice-cream"></i>
          <p></p>
        </li>
        <router-link to="/user_address" tag="li">
          <i style="color:#5A9FEC" class="el-icon-hot-water"></i>
          <p></p>
        </router-link>
        <li>
          <i style="color:#5A9FEC" class="el-icon-tableware"></i>
          <p></p>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
import axios from "axios";
import toLocal from "../utils/tolocal";
export default {
  //import
  components: {},
  data() {
    //
    return {
      chack: false,
      tokens: "",
      user: {}
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    waitestimate(){
      this.$router.push({
        path:"/waitestimate"
      })
    }
  },
  // - this
  created() {
    let tok = JSON.parse(localStorage.getItem("tokens"));
    console.log(tok);
    this.$store.state.tokens = tok.token;
    this.tokens = this.$store.state.tokens;
    console.log(this.tokens);
    axios
      .post(`https://api.it120.cc/small4/user/detail?token=${this.tokens}`)
      .then(res => {
        if (res.data.code == 0) {
          this.chack = true;
          this.user = res.data.data.base;
        }
      });
  },
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style lang='scss' scoped>
//@import url(); css
* {
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 0.16rem;
}
.mine {
  width: 100%;
  .mine_top {
    width: 100%;
    height: 3rem;
    background-color: #b9aa83;
    display: flex;
    align-items: center;
    justify-content: space-around;
    p {
      width: 30%;
      text-align: end;
      i {
        font-size: 1rem;
        color: white;
      }
    }
    div {
      width: 87%;
      color: white;
    }
  }
  .mine_banner {
    width: 100%;
    height: 1rem;
    background-color: white;
    line-height: 1rem;
    color: red;
    text-indent: 0.2rem;
  }
  .nothing {
    width: 100%;
    height: 0.2rem;
    background-color: #f5f5f5;
  }
  .mine_order {
    width: 100%;
    height: 1rem;
    display: flex;
    align-items: center;
    text-indent: 0.2rem;
    border-bottom: 1px solid #ddd;
    div {
      width: 80%;
      display: flex;
      align-items: center;
      i {
        font-size: 0.4rem;
        color: gray;
      }
    }
  }
  .mine_nav {
    width: 100%;
    height: 1.2rem;
    ul {
      display: flex;
      justify-content: space-around;
      align-items: center;
      li {
        text-align: center;
        margin-top: 0.1rem;
        width: 25%;
        i {
          font-size: 0.5rem;
          color: gray;
          margin-bottom: 0.1rem;
        }
      }
    }
  }
  .mine_banner1 {
    width: 100%;
    height: 5rem;
    ul {
      display: flex;
      justify-content: space-around;
      align-items: center;
      flex-wrap: wrap;
      li {
        width: 33.3%;
        margin-top: 0.5rem;
        text-align: center;
        i {
          font-size: 0.5rem;
        }
      }
    }
  }
}
</style>


